  .sox {
    display: inline-block;
    vertical-align: middle;
    font-size: 18px;
    color: #999;
    background: #f8f8f8;
    padding: 0 12px;
    height: 35px;
    line-height: 37px;
    margin-right: 10px;
    cursor: pointer;
    transition: all .3s;
  }
  .noidungsox {
